.airport-wrapper {
    max-width: 1760px;
    width: 100%;
    padding: 40px 0;
    display: inline-flex;
    justify-content: flex-start;

    @include max-screen(1700px) {
        flex-direction: column;
    }
}

.airport-flex-wrap {
    display: inline-flex;
    justify-content: flex-start;

    @include max-screen(1200px) {
        padding-bottom: 30px;
        border-bottom: 1px solid $light_white;
    }

    @include max-screen(768px) {
        flex-direction: column;

    }

}

.tariffs-airport {
    max-width: 1120px;
    width: 100%;
    border-top-right-radius: 7px;
    border-bottom-right-radius: 7px;
}

.airport-inclusive {
    max-width: 240px;
    width: 100%;
    border-left: 0.5px solid $white;
    padding-left: 30px;

    @include max-screen(768px) {
        border-left: none;
        padding-top: 30px;
        padding-left: 0;
    }

    p {
        color: $white;
        font-size: 16px;
        letter-spacing: 1px;
        line-height: 1.4;
    }
}

.taxi-wrapper {
    position: relative;
    height: 360px;
    max-width: 1120px;
    width: 100%;
    background-image: url("/assets/img/taxi.jpg");
    background-size: cover;
    border-top-right-radius: 7px;
    border-bottom-right-radius: 7px;
    background-repeat: no-repeat;
    background-position: center;

    @include max-screen(1700px) {
        border-radius: 0;
    }

}

.tariffs-rules {
    position: absolute;
    bottom: 40px;
    left: 40px;
    display: inline-block;
    max-width: 300px;
    width: 100%;
    height: 45px;
    line-height: 45px;
    background: $white;
    border-radius: 7px;

    @include max-screen(480px) {
        left: 10px;
        max-width: 290px;
    }

    &__text {
        width: 255px;
        margin: 0 auto;
        color: $orange;
        font-style: italic;
        letter-spacing: 1px;
    }
}

.app-store {
    margin-right: 25px;
}

.google-play,
.app-store {
    margin-top: 50px;
    width: 188px;
}

.app-store {
    @include max-screen(576px) {
        margin-top: 30px;
    }
}

.tariffs-important-info {
    padding: 30px 0;
    max-width: 1760px;
    width: 100%;
    margin: 0 auto;

    @include max-screen(1700px) {
        max-width: 1200px
    }

    @include max-screen(1200px) {
        max-width: 560px
    }

    &__text {
        font-size: 16px;
        color: $blue;
        letter-spacing: 0.5px;
    }
}

.separator-airport {
    height: 0.5px;
    width: 72px;
    background-color: #fff;
    transform: rotate(90deg);
    margin-top: 43px;
    margin-left: -33px;
    margin-right: 20px;
}




